import router from './router'
import store from './store'
import NProgress from 'nprogress' // Progress 
import 'nprogress/nprogress.css'// Progress 
import { getToken } from '@/utils/token'

const whiteList = ['/login'] // ,

router.beforeEach((to, from, next) => {
  NProgress.start() // Progress
  // cookietoken
  if (getToken()) {
    // token
    if (to.path === '/login') {
      // 
      // 
      next({ path: '/' })
    } else {
      // 
      // VUEX
      if (store.getters.roleName === null) {
        // 
        store.dispatch('Detail').then(response => {
          // 
          store.dispatch('GenerateRoutes', response.data).then(() => {
            router.addRoutes(store.getters.addRouters)
            next({ ...to })
          })
        })
      } else {
        next()
      }
    }
  } else {
    // ,
    if (whiteList.indexOf(to.path) !== -1) {
      next()
    } else {
      // ,,
      next('/login')
      NProgress.done() // Progress
    }
  }
})

router.afterEach(() => {
  NProgress.done() // Progress
})



