const _import = require('./_import_' + process.env.NODE_ENV)

Vue.use(Router)

/**
 * icon : the icon show in the sidebar
 * hidden : if `hidden:true` will not show in the sidebar
 * redirect : if `redirect:noRedirect` will not redirect in the levelBar
 * noDropDown : if `noDropDown:true` will not has submenu in the sidebar
 * meta : `{ permission: ['a:xx'] }`  will control the page permission
 **/
export const constantRouterMap = [
  { path: '/login', component: _import('login/index'), hidden: true },
  { path: '/404', component: _import('errorPage/404'), hidden: true },
  { path: '/401', component: _import('errorPage/401'), hidden: true },
  {
    path: '/refresh',
    hidden: 'true',
    component: resolve => require(['@/views/refresh'], resolve),
    meta: {
      title: ''
    }
  },
  // {
  //   path: '/',
  //   component: Layout,
  //   redirect: '/dashboard',
  //   children: [
  //     {
  //       path: 'dashboard', component: () => import('dashboard/index'),
  //       name: 'Dashboard',
  //       meta: { title: 'Dashboard', icon: 'dashboard', affix: true }
  //     }
  //   ]
  // }

  {
    path: '',
    component: Layout,
    name: 'Dashboard',
    icon: 'dashboard',
    children: [
      { path: 'Dashboard', name: '', component: _import('dashboard/dashboard/index'), meta: { title: '', icon: 'dashboard', noCache: true, affix: true }},
      { path: 'myinfo', name: '', component: _import('dashboard/myinfo/index'), meta: { title: '', icon: 'dashboard', noCache: true }}
    ]
  }
]

export default new Router({
  // mode: 'history', //
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRouterMap
})

